import React from 'react';
import { createPortal } from 'react-dom'
import ProductsList from './components/Products/ProductsList'
import Cart from './components/layout/Cart/Index'
import { Provider } from './Contexts/CartContext'
import ProductDetail from './components/Products/ProductDetail'

const CartPortal = (props) => {
	const cartTag = document.getElementById('cart')
	console.log(cartTag)
	return cartTag ? createPortal(<Cart />, cartTag) : null
}
const ProductsPortal = (props) => {
	let { pageData } = props

	const productsTag = document.getElementById('products')
	const singleProductTag = document.getElementById('single-product')
	if (singleProductTag) {
		return singleProductTag ? createPortal(<ProductDetail />, singleProductTag) : null
	}
	else {
		return productsTag ? createPortal(<ProductsList startArgs={pageData} />, productsTag) : null
	}
}

let App = () => {
	return (
		<Provider>
			<div className="app">
				<CartPortal />
				<ProductsPortal pageData="somedata" />
			</div>
		</Provider>
	);
}

export default App;