import React, { Component } from 'react'
import productInfo from './product.json'

export class MultipleImages extends Component {
    state = {
        mainImage: productInfo.imagens
    }
    changeImage = (e) => {
        e.preventDefault()
        this.setState({ mainImage: e.target.src })
    }
    render() {
        let imagens = this.state.mainImage

        return (
            <div className="images">
                <figure className="main-image">
                    <img src={imagens} alt="" />
                </figure>
                <ul className="list">
                    <li>
                        <img src="http://res.cloudinary.com/lucaspedroso/image/upload/v1559421268/wmih1qdpxwnnjmvfkllx.jpg" alt="product 1" onClick={this.changeImage} />
                    </li>
                    <li>
                        <img src="http://res.cloudinary.com/lucaspedroso/image/upload/v1559421471/qsygycpccpvhpdletnvz.jpg" alt="product 2" onClick={this.changeImage} />
                    </li>
                    <li>
                        <img src="http://res.cloudinary.com/lucaspedroso/image/upload/v1559422184/bmo6benpf8bjjinoszrz.png" alt="product 3" onClick={this.changeImage} />
                    </li>
                </ul>
            </div>
        )
    }
}

export default MultipleImages
