import React, { Component } from 'react'
import axios from 'axios'
import Product from '../Products/Product'
export default class ProductsList extends Component {
    state = {
        products: []
    }
    componentDidMount() {


        let counter = window.counter;
        let url = `/products/api/${counter}`
        if (window.search) {
            if (window.search.length) {
                this.setState({ data: window.search })
            }
        }
        else {
            axios.get(url).then(response => {
                if (response.data) {
                    this.setState({ data: response.data })
                } else {
                    this.setState({
                        data: null,
                    })
                }
            }).catch(err => {
                this.setState({ data: null })
            })
        }
    }
    render() {
        let { data } = this.state
        return (
            <div className="row">
                {data && data.map(item => {
                    return (
                        <Product key={item.id} name={item.nome} id={item.id} price={item.valorVenda} image={item.imagens} quantidade={item.quantidade} />
                    )
                })}
                {!data && <div> We got an error, try reload the page </div>}
            </div>
        )
    }
}
